import { SET_FILTER } from '../actions';

const filter = (state = { ALLOW_PRESENT_IND: true }, action) => {
	switch (action.type) {
	case SET_FILTER: {
		const newState = Object.assign({}, state, {});
		newState[action.filter] = action.status;
		return newState;
	}
	default: {
		return state;
	}
	}
};

export default filter;



